import React, { useState, useRef } from 'react'
import styled from 'styled-components'

import { FormData } from '../FormStep1/types/FormData'
import { messagesIndexedByNameAttributeWithBooleanValue } from '../dataProcessing'

import TextInput from '../TextInput'
import { ButtonStates } from '../ProgressButton'
import HorizontalLine from '../HorizontalLine'

export type Props = {
  onFormValidatedProp: (isValidated: boolean) => void,
  onClickOnContinueButtonProp: (formData: any) => void
}

const BillingAddressFieldsContainer = styled.div`
    max-width: 655px;
    @media (max-width: 768px) {
        margin-left: 0;
        padding: 15px;
    };
`

const BillingAddressFieldsForm = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 654px;
    height: 100%;
    @media (max-width: 768px) {
        display: block;
        width: 100%;
    }
`

const InputFieldContainer = styled.div`
    margin-top: 1.0625rem;
`


const BillingAddressFields: React.FC<Props> = ({
  onFormValidatedProp }) => {
  interface validationByName {
    [label: string]: boolean,
  }

  interface ReactRef {
    [label: string]: React.RefObject<HTMLInputElement>,
  }


  const [] = useState(false)
  const [validationInfo, updateValidationInfo] = useState(false)
  const Refs: ReactRef = {
    ["firstName"]: useRef<HTMLInputElement>(null),
    ["lastName"]: useRef<HTMLInputElement>(null),
    ["billingAddress"]: useRef<HTMLInputElement>(null),
    ["unitNumber"]: useRef<HTMLInputElement>(null),
    ["city"]: useRef<HTMLInputElement>(null),
    ["stateTerritory"]: useRef<HTMLInputElement>(null),
    ["postcode"]: useRef<HTMLInputElement>(null),
  }

  const validation: validationByName = {
    ["firstName"]: false,
    ["lastName"]: false,
    ["billingAddress"]: false,
    ["unitNumber"]: false,
    ["city"]: false,
    ["stateTerritory"]: false,
    ["postcode"]: false,
  }

  const getValidate = () =>
    validation["firstName"]
    && validation["lastName"]
    && validation["billingAddress"]
    && validation["unitNumber"]
    && validation["city"]
    && validation["stateTerritory"]
    && validation["postcode"]

  const inputFieldFactory = (children: React.ReactNode) => {
    return (
      <InputFieldContainer>
        {children}
      </InputFieldContainer>)
  }

  const createInputField = (
    label: string,
    placeHolderParam: string,
    nameParam: string,
    maxLength?: number,
    aRef?: React.RefObject<HTMLInputElement>,
    width?: string): React.ReactNode => {
    return (
      <TextInput
        placeholder={placeHolderParam}
        name={nameParam}
        type="text"
        maxLength={maxLength}
        {...{ label, message: messagesIndexedByNameAttributeWithBooleanValue }}
        onInputSucess={wasAnInputSuccess => {
          validation[nameParam] = wasAnInputSuccess
          updateValidationInfo(getValidate())
        }}
        ref={aRef}
        widthSize={width}
      />
    )
  }


  return (
    <BillingAddressFieldsContainer>
      <BillingAddressFieldsForm>

        {inputFieldFactory(
          createInputField("Your first name", "Your first name", "firstName",
            undefined, Refs["firstName"])
        )}

        {inputFieldFactory(
          createInputField("Your last name", "Your last name", "lastName",
            undefined, Refs["lastName"])
        )}

        {inputFieldFactory(
          createInputField("Billing Address", "Type Address here", "billingAddress",
            undefined, Refs["billingAddress"], "37.75rem")
        )}

        {inputFieldFactory(
          createInputField("Unit number (if applicable)", "Type unit number", "unitNumber",
            undefined, Refs["unitNumber"])
        )}

        {inputFieldFactory(
          createInputField("City", "Type city", "city",
            undefined, Refs["city"])
        )}

        {inputFieldFactory(
          createInputField("State / territory", "Type state / territory", "stateTerritory",
            undefined, Refs["stateTerritory"])
        )}

        {inputFieldFactory(
          createInputField("Postcode", "Type postcode", "postcode",
            undefined, Refs["postcode"])
        )}

      </BillingAddressFieldsForm>
      <div style={{
        marginTop: `3.125rem`,
        marginBottom: `3.125rem`
      }}>
        <HorizontalLine />
      </div>

    </BillingAddressFieldsContainer >
  )
}

export default BillingAddressFields
