import React from 'react'
import styled from 'styled-components'
import ButtonItem from './ItemButton'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons'

export const HOME_ADDRESS_SELECTION = "homeAddress"
export const BILLING_ADDRESS_SELECTION = "billingAddress"

const StyledIconChecked = styled(FontAwesomeIcon)`
      color: #189126;
      width: 22px;
      height: 22px;
      text-align: center;
`

const Container = styled.div`
  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
`

const ContainerInfo = styled.div`
    display: flex,
    justify-content: flex-start;
    flex-direction: row;
    align-items: center;
    margin-top: 11px
`
interface Props {
  className?: string
  onSelectedLabel?: (value: string) => void
}
export const CheckboxButtonGroup: React.FC<Props> = ({
  className,
  onSelectedLabel }) => {
  const [activeId, changeActiveId] = React.useState(3)

  return (
    <div className={className}>
      <Label>
        Billing address
        {' '}
        <LabelIcon onInputSucessAsLabelIconColor={true}>*</LabelIcon>
      </Label>

      <Container>
        <ButtonItem
          active={activeId == 0}
          title={'Same as home address'}
          onPress={() => {
            changeActiveId(0)
            onSelectedLabel ? onSelectedLabel(HOME_ADDRESS_SELECTION) : {}
          }} />
        <ButtonItem
          active={activeId == 1}
          title={'Use a different billing address'}
          onPress={() => {
            changeActiveId(1)
            onSelectedLabel ? onSelectedLabel(BILLING_ADDRESS_SELECTION) : {}
          }} />
      </Container>
      <ContainerInfo>
        <StyledIconChecked icon={faCheckCircle} style={{ display: activeId == 3 ? 'none' : 'block' }} />
      </ContainerInfo>
    </div>
  )
}

export default CheckboxButtonGroup
