import React from 'react'
import styled from 'styled-components'

interface Spinner {
  showHide?: string,
}
export type Props = {
  className: string
}

const SpinnerWrapper = styled.div`
  background: linear-gradient(to right, rgba(255, 255, 255, 1) 70%, rgba(112, 112, 112, .3));
  width: 23px;
  height: 23px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  animation: spin 2s linear infinite;

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
`

const StyledSpinner = styled.div`
  width: 17px;
  height: 17px;
  background: rgba(255, 104, 32, 1);
  border-radius: 50%;
`

export const Spinner: React.FC<Props> = ({ className }) => (
  <SpinnerWrapper className={className} role='progressbar'>
    <StyledSpinner />
  </SpinnerWrapper>
)

