import React from 'react'
import styled from 'styled-components'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSquare } from '@fortawesome/free-regular-svg-icons'
import { faCheck } from '@fortawesome/free-solid-svg-icons'

const Container = styled.div<PropsActive>`
    background: ${props => props.active ? '#E2F1F8' : '#F3F3F3'};
    border-radius: 10px;
    opacity: 1;
    padding: 20px 40px;
    margin-right: 10px;
    margin-top: 10px;
    @media screen and (max-width: 768px) {
        width: 100%;
        align-items: center;
        justify-content: flex-start;
        margin-right: 0px;
        margin-left: 0px;
        padding: 20px 0;
    };
    display: flex;
    flex-direction: row;
    cursor: pointer;
`
const StyledTitle = styled.div<PropsActive>`
  font-family: museo-sans, sans-serif;
  font-weigth: 500;
  font-size: 16px/26px;
  letter-spacing: -0.05px;
  text-align: center;
  color:${props => props.active ? '#052948' : '#757575'};
`
const StyledIcon = styled(FontAwesomeIcon) <PropsActive>`
  color: ${props => props.active ? '#ff6820' : '#757575'};
  width: 22px;
  height: 22px;
  text-align: center;
  margin-right: 20px;
  @media screen and (max-width: 768px) {
    margin-left: 15px;
  }
`

interface Props {
  active: boolean
  onPress: any
  title: string
}
interface PropsActive {
  active: boolean
}
export const ButtonItem: React.FC<Props> = (props) => {
  return (
    <Container
      active={props.active}
      onClick={() => { props.onPress() }}>
      <StyledIcon
        active={props.active}
        icon={props.active ? faCheck : faSquare} />
      <StyledTitle active={props.active}>
        {props.title}
      </StyledTitle>
    </Container>
  )
}
export default ButtonItem