import React from 'react'
import styled from 'styled-components'
import color from '../color-css/color'

export type Props = {
  className?: string,
  textContent: string
}

const StylingH3 = styled.h3`
  color: ${color.blue_17_percent};
  font-weight: 900;
  font-family: museo-sans, sans-serif;
  text-align: left;
  letter-spacing: -0.08px;
`

export const Headline: React.FC<Props> = ({ className, textContent }) => {
  return <StylingH3 className={className}>{textContent}</StylingH3>
}

export default Headline
