import React, { useState } from 'react'
import styled from 'styled-components'

import CheckboxButtonGroup, { BILLING_ADDRESS_SELECTION } from '../CheckboxButtonGroup'
import RadioButtonGroup from '../RadioButtonGroup'
import Headline from '../Headline'
import BankAccountTab from '../BankAccountTab'
import CreditCardPaymentTab from '../CreditCardPayment'
import PayDeductionTab from '../PayDeductionTab'
import BillingAddressFields from '../BillingAddress/BillingAddressFields'
import ProgressButton, { ButtonStates } from '../ProgressButton'

export type Props = {
  onFormValidatedProp: (isValidated: boolean) => void,
}

const PositioningHeadLine = styled(Headline)`
  margin-bottom: 2.5rem;
`

const PositioningRadioButtonGroup = styled(RadioButtonGroup)`
  margin-top: 2.5rem;
`
const FormStepFourContainer = styled.div`
  padding: 15px;
`

const FormStepFour: React.FC<Props> = ({
  onFormValidatedProp
}) => {

  const [paymentTab, setPaymentTab] = useState(-1)
  const [selectedBillingAddress, setSelectedBillingAddress] = useState(false)
  const [validationInfo, updateValidationInfo] = useState(false)

  const onFormIsValidated = () => {
    onFormValidatedProp(validationInfo)
    return validationInfo ? ButtonStates.enabled : ButtonStates.disabled
  }

  return (
    <FormStepFourContainer>
      <PositioningHeadLine textContent="Your contribution" />
      <CheckboxButtonGroup
        onSelectedLabel={selectedLabel => {
          setSelectedBillingAddress(selectedLabel === BILLING_ADDRESS_SELECTION)
        }}
      />

      {selectedBillingAddress
        && <BillingAddressFields
          onFormValidatedProp={isValidated => { }}
          onClickOnContinueButtonProp={formData => { }}
        />}

      <PositioningRadioButtonGroup
        onSelectTabProp={tabNumber => {
          setPaymentTab(tabNumber)
        }}
      />
      {paymentTab === 0 && <BankAccountTab />}

      {paymentTab === 1 && <CreditCardPaymentTab />}

      {paymentTab === 2 &&
        <PayDeductionTab
          expandableParagraphIsLongTextProp={true}
          expandableParagraphDescriptionTextProp={'I HEREBY apply for membership of the Shop, Distributive and Allied Employees Association, South Australian Branch as registered under the Fair Work (Registered Organisations) Act 2009 (Cth) and (if eligible) the Shop, Distributive and Allied Employees Association, South Australian Branch as registered under the Fair Work Act 1994 (SA). I agree to comply with and be bound by the Rules of each Association (SDA) as ammended from time to time. I acknowledge the SDA is affiliated to the Australian Council of Trade Unions, the Australian Labor Party and Union Network International. I authorise the SDA to use my personal information in order to communicate with me, for direct marketing, research and member surveys. Privacy Act 1998 - The SDA is bound by the Privacy Act. This information is collected to enable the SDA to contact you about matters relating to your SDA membership, and to ensure that we have the necessary information to represent your employment and related interests. A copy of the SDAs Privacy Statement is available from the SDA office and our website'}
        />
      }

      <div
        style={{
          marginTop: `60px`,
          marginBottom: `115px`
        }}
      >
        <ProgressButton
          title="Finish & submit"
          buttonStates={
            onFormIsValidated()
          }
          onClickCallbackProp={() => {
          }} />
      </div>
    </FormStepFourContainer>
  )
}

export default FormStepFour
