import React from 'react'
import styled from 'styled-components'
import ButtonItem from './ItemButton'
import Label from '../Label'
import LabelIcon from '../Label/LabelIcon'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckCircle } from '@fortawesome/free-solid-svg-icons'

const StyledIconChecked = styled(FontAwesomeIcon)`
      color: #189126;
      width: 22px;
      height: 22px;
      text-align: center;
`
const Container = styled.div`
  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
`
const ContainerInfo = styled.div`
    display: flex,
    justify-content: flex-start;
    flex-direction: row;
    align-items: center;
    margin-top: 11px
`

const RadioButtonContaiter = styled.div`
  @media screen and (max-width: 768px) {
    // padding: 15px;
  };
`

export type Props = {
  className?: string,
  onSelectTabProp: (tabNumber: number) => void
}

export const RadioButtonGroup: React.FC<Props> = ({
  className, onSelectTabProp
}) => {
  const [activeId, changeActiveId] = React.useState(3)

  return (
    <RadioButtonContaiter className={className}>
      <Label>Payment method
        {' '}
        <LabelIcon onInputSucessAsLabelIconColor={true}>*</LabelIcon>
      </Label>
      <Container>
        <ButtonItem
          active={activeId == 0}
          title={'Bank account'}
          onPress={() => {
            changeActiveId(0)
            onSelectTabProp(0)
          }} />
        <ButtonItem
          active={activeId == 1}
          title={'Credit card'}
          onPress={() => {
            changeActiveId(1)
            onSelectTabProp(1)
          }} />
        <ButtonItem
          active={activeId == 2}
          title={'Payroll deduction'}
          onPress={() => {
            changeActiveId(2)
            onSelectTabProp(2)
          }} />
      </Container>
      <ContainerInfo>
        <StyledIconChecked icon={faCheckCircle} style={{ display: activeId == 3 ? 'none' : 'block' }} />
      </ContainerInfo>
    </RadioButtonContaiter>
  )
}
export default RadioButtonGroup
