import React from 'react'
import styled from 'styled-components'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faInfo } from '@fortawesome/free-solid-svg-icons'
import { isMobile } from 'react-device-detect'
import color from '../color-css/color'

const TooltipIconContainer = styled.div`
    width: 1.25rem;
    height: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${color.tooltip_icon};
    border-radius: 50%;
`

const StyledIcon = styled(FontAwesomeIcon)`
    color: ${color.white};
    width: 0.75rem !important;
    height: 0.625rem;
`

// TooltipExpandable
const TooltipExpandable = styled.div`
    display: none;
    min-width: 13.625rem;
    position: relative;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    background: ${color.white} 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 3.125rem #012F571A;
    top: 1.25rem;
    right: 1.25rem;
    z-index: 1111111111;
    &:before {
        content: "";
        position: absolute;
        left: 1.25rem;
        top: -1.125rem;
        width: 0;
        height: 0;
        border-top: 1.25rem solid transparent;
        border-right: 1.625rem solid ${color.white};
        border-bottom: 0.8125rem solid transparent;
        transform: rotate(90deg);
    }
`

interface TooltipContainer {
    tooltipName: string,
    visibility: boolean,
    widthSize?: string
}
// TooltipContainer
const TooltipContainer = styled.div<TooltipContainer>`
    display: ${props => props.visibility === true ? "none" : "flex"};
    flex-wrap: wrap;
    min-width: 13.625rem;
    max-width: ${props => props.widthSize ? props.widthSize : "18.1875rem"};
    height: 1.25rem;
    border-radius: 50%;
    vertical-align: middle;
    cursor: pointer;
    &:hover ${TooltipExpandable} {
        display: block;
    }
`

const TooltipExpandableContent = styled.div`
    font-family: museo-sans, sans-serif;
    padding: 0.9375rem 1.1325rem 0.875rem 0.9375rem;
    text-align: left;
    font-weight: 500;
    font-size: 0.875rem;
    letter-spacing: -0.0025rem;
    color: ${color.grey_29_percent};
`

interface TooltipMessage {
    tooltipName: string
}
const TooltipMessage = styled.span<TooltipMessage> `
    width: 200px;
    font-family: museo-sans, sans-serif;
    font-style: italic;
    font-weight: 500;
    margin-left: 0.625rem;
    font-size: 1rem;
    margin-left: 0.5rem;
    cursor: pointer;
`

const format = (input: string) => {
    let test = /before/g;
    const result = input.replace(test, "<span style='font-weight: bold; font-style: italic'>before</span>")
    return result
}

export const Tooltip = (props: any) => {
    if (isMobile) {
        return (
            <div>
                <TooltipContainer
                    visibility={props.visibility}
                    tooltipName={props.tooltipName}
                >
                    <TooltipIconContainer>
                        <StyledIcon icon={faInfo} />
                    </TooltipIconContainer>
                    <TooltipMessage tooltipName={props.tooltipName}>
                        {props.tooltipTitle}
                    </TooltipMessage>
                    <TooltipExpandable>
                        <TooltipExpandableContent dangerouslySetInnerHTML={{
                            __html: props.tooltip_message
                        }}>
                        </TooltipExpandableContent>
                    </TooltipExpandable>
                </TooltipContainer>
            </div>
        )
    }
    return (
        <div>
            <TooltipContainer
                visibility={props.visibility}
                tooltipName={props.tooltipName}
                widthSize={props.widthSize}>
                <TooltipIconContainer>
                    <StyledIcon icon={faInfo} />
                </TooltipIconContainer>
                <TooltipMessage tooltipName={props.tooltipName}>
                    {props.tooltipTitle}
                </TooltipMessage>
                <TooltipExpandable>
                    <TooltipExpandableContent dangerouslySetInnerHTML={{
                        __html: props.tooltip_message
                    }}>
                    </TooltipExpandableContent>
                </TooltipExpandable>
            </TooltipContainer>
        </div>
    )
}

export default Tooltip;